import React, {Component} from 'react';
import './LangSwitch.sass';

export default class LangSwitch extends Component {
    onChangeLang = () => {
        this.props.onChangeLang();
    }

    render() {
        return (
            <div className='langSwitch'>
                <p>Ru</p>
                <input type="checkbox"  id='switch' onChange={this.onChangeLang}/>
                <label htmlFor='switch'></label>
                <p>En</p>
            </div>
        )
    }
}