import React, {Component} from 'react';
import './AboutMe.sass';
import AboutMeText from './AboutMeText';
import about from './about.svg'

export default class AboutMe extends Component {
    onOpenClick = () => {
        document.querySelector('.showFull').style.display = 'none';
        document.querySelector('.showShort').style.display = 'block';
        document.querySelector('.shortArticle').classList.add('fullArticle');
    }

    onCloseClick = () => {
        document.querySelector('.showFull').style.display = 'block';
        document.querySelector('.showShort').style.display = 'none';
        document.querySelector('.shortArticle').classList.remove('fullArticle');
    }

    render() {
        const {isRu} = this.props;
        const {name, city, age, education, english, lastWork, hobby, aboutMe} = isRu ? AboutMeText[0] : AboutMeText[1];

        return(
            <section className='aboutMe container'><a name='aboutMe'/>
                <h3>{name}</h3>
                <hr/>
                <div className='greyText'>
                    <h6>{isRu ? 'Город: ' : 'City: '}{city}</h6>
                    <h6>{isRu ? 'Возраст: ' : 'Age: '}{age}</h6>
                </div>
                <hr/>
                <div className='aboutContent'>
                    <img src={about} alt="Illustration"/>
                    <div className='article'>
                        <article className='shortArticle'>
                            <span>{isRu ? 'Образование: ' : 'Education: '}</span>
                            <p>{education}</p>
                            <span>{isRu ? 'Английский язык: ' : 'English: '}</span>
                            <p>{english}</p>
                            <span>{isRu ? 'Предыдущее место работы: ' : 'Previous work place: '}</span>
                            <p>{lastWork}</p>
                            <span>{isRu ? 'Хобби: ' : 'Hobby: '}</span>
                            <p>{hobby}</p>
                            <span>{isRu ? 'О себе: ' : 'About me: '}</span>
                            {aboutMe.map(elem => {
                                return(
                                    <p key={elem.slice(0,10)}>{elem}</p>
                                )})}
                        </article>
                        <button className='showFull' onClick={this.onOpenClick}>{isRu ? 'Показать полностью...' : 'Show in full...'}</button>
                        <button className='showShort'onClick={this.onCloseClick}>{isRu ? 'Скрыть' : 'Hide'}</button>
                    </div>
                </div>
            </section>
        )
    }
}