import React, {Component} from 'react';
import './Skills.sass';
import SkillListRu from './SkillListRu';
import SkillListEn from './SkillListEn';
import SkillBlock from './SkillBlock';

export default class Skills extends Component {
    state = {
        activeBlock : SkillListRu[0]
    }

    componentDidMount() {
        const isRu = this.props;
        this.setState( {
            activeBlock : isRu ? SkillListRu[0] : SkillListEn[0]
        })
    }

    onClickMenu = (i, SkillList) => {
        this.setState({activeBlock : SkillList[i]})
    }

    render() {
        const {isRu} = this.props;
        const {activeBlock} = this.state;
        const SkillList = isRu ? SkillListRu : SkillListEn;
    
        const menuElements = SkillList.map((elem, i) => {
            let classList = 'skillMenuButton';
            const {id} = elem;

            if (id == activeBlock.id) {
                classList += ' activeButton'
            }

            return (
                <button className={classList} key={i} onClick={()=>this.onClickMenu(i, SkillList)}>{elem.title}</button>
            )}
        )

        return(
            <section className = 'skills'><a name='skills'/>
                <div className='container'>
                    <h3>{isRu ? 'Навыки' : 'Skills'}</h3>
                    <hr/>
                    <div className = 'skillMenu'>
                        {menuElements}
                    </div>
                    <hr/>
                    <div className = 'skillsContainer'>
                        <SkillBlock id={activeBlock.id} isRu={isRu}/>
                    </div>
                </div>
            </section>
        )
    }
}