import React, {Component} from 'react';
import './BurgerMenu.sass';

export default class BurgerMenu extends Component {
    onClickMenu = () => {
        {
            let menuBtn = document.querySelector('.menu-btn'),
                menu = document.querySelector('.menu');
            if (menuBtn.classList.contains('menu-btn_active')) {
                menuBtn.classList.remove('menu-btn_active');
                menu.classList.remove('menu_active')
            } else {
                menuBtn.classList.add('menu-btn_active');
                menu.classList.add('menu_active');
            }             
        }
    }   
    render() {
        const {content} = this.props;

        return(
            <div className='burger-menu'>
                <a href="#" className='menu-btn' onClick={this.onClickMenu}>
                    <span></span>
                </a>
                <nav className='menu'>
                    <a href="#contacts">{content.menu1}</a>
                    <a href="#aboutMe">{content.menu2}</a>
                    <a href="#skills">{content.menu3}</a>
                    <a href="#projects">{content.menu4}</a>
                </nav>
            </div>
        )
    }
}
