import React from 'react';
import './Contacts.sass';
import mail from './mail.png';
import phone from './smartphone.png';

const Contacts = ({isRu}) =>  {
    return(
        <section className='contacts'><a name='contacts'/>
            <div className='contactsBlock container'>
                <h3>{isRu ? 'Контакты' : 'Contacts'}</h3>
                <div className='myContact'>
                    <img src={mail} alt="mail-icon" /><span>lest1995@mail.ru</span>
                </div>
                <div className='myContact'>
                    <img src={phone} alt="phone-icon" /><span>+7 (965) 083-12-54</span>
                </div>
            </div>
        </section>
    )
}

export default Contacts;