import React, {Component} from 'react';
import './SkillBlock.sass';
import slide1 from '../svg/slide1.svg';
import slide2 from '../svg/slide2.svg';
import slide3 from '../svg/slide3.svg';
import slide4 from '../svg/slide4.svg';
import slide5 from '../svg/slide5.svg';
import SkillListRu from '../SkillListRu';
import SkillListEn from '../SkillListEn';

export default class SkillBlock extends Component {

    render() {
        const {id} = this.props;
        const skill = this.props.isRu ? SkillListRu[id-1] : SkillListEn[id-1];
        const slides = [slide1, slide2,slide3,slide4,slide5]
        const elements = skill.list.map((element) => {
            return(
                <li key={'innerIdFor'+element}>{element}</li>
            )
        })
    
        return(
            <div className = 'skillBlock'>
                <span>{skill.description}</span>
                <div className='skillList'>
                    <div className = 'first'>
                        <ul>
                            {elements}
                        </ul>
                        <p>{skill.content}</p>
                    </div>
                    <img src={slides[skill.id-1]} alt="Illustration" />
                </div>
            </div>
        )
    }
}
