import React, {Component} from 'react';
import './SeperateProject.sass';

export default class SeparateProject extends Component {
    render() {
        const {projectId, isRu} = this.props;

        return(
            <div className = 'separateProjectContainer'>
                <div className = 'projectCardContainer'>
                    <div className = 'projectCard'>
                        <div className = 'frontCard'>
                            <h3>{projectId.name}</h3>
                            <img src={projectId.img} alt="ProjectImg" />
                        </div>
                        <div className = 'backCard'>
                            <span>{projectId.skills}</span>
                        </div>
                    </div>
                </div>
                <a href = {projectId.path} target='_blank' className = 'goToProject'>{isRu ? 'Перейти к проекту' : 'Go to the project'}</a>
            </div>
        )
    }
}