let currentAge = () => {
    const today = new Date(),
        birthday = new Date('1995, 06, 12'),
        month = today.getMonth() - birthday.getMonth(),
        days = today.getDate() - birthday.getDate();
    let age = today.getFullYear() - birthday.getFullYear();
    if (month < 0) {
        age--;
    } else if (month == 0 && days < 0) {
        age--;
    }
    return age;
}

