import React, {Component} from 'react';
import './Projects.sass';
import SeparateProject from './SeparateProject/';
import projectsInformationRu from './ProjectsInformationRu';
import projectsInformationEn from './ProjectsInformationEn';

export default class Projects extends Component {
    render() {
        const {isRu} = this.props;
        const projectsInformation = isRu ? projectsInformationRu : projectsInformationEn;
        
        return(
            <section className = 'projects'><a name='projects'/>
                <div className='container'>
                    <h3>{isRu ? 'Проекты' : 'Projects'}</h3>
                    <hr/>
                    <div className = 'projectsContainer'>
                        <SeparateProject projectId = {projectsInformation.first} isRu={isRu}/>
                        <SeparateProject projectId = {projectsInformation.second} isRu={isRu}/>
                        <SeparateProject projectId = {projectsInformation.third} isRu={isRu}/>
                    </div>
                </div>
            </section>
        )
    }
}
