import React, {Component} from 'react';
import './Preview.sass';
import previewContent from './previewContent';
import BurgerMenu from './BurgerMenu';
import LangSwitch from './LangSwitch';

export default class Preview extends Component {
    render() {
        const {isRu} = this.props;
        const content = isRu ? previewContent[0] : previewContent[1];

        return(
            <section className='avatarBlock container'>
                <div className='menuLine'>
                    <BurgerMenu content={content}/>
                    <LangSwitch onChangeLang = {this.props.onChangeLang}/>
                </div>
                <div className='preview'>
                    <h1>{content.cv}</h1>
                    <h1>{content.hello}</h1>
                    <h1>{content.prof}</h1>
                </div>
            </section>
        )
    }
}
